@import "../../variables.scss";

.header {
  width: 100vw;
  position: sticky;
  top: 0;
  left: 0;
  display: flex;
  background-color: white;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: $font-l;
  padding: 0em 2.5em;
  box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset,
    rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  img {
    width: 120px;
    cursor: pointer;
  }

  nav {
    a {
      text-decoration: none;
      color: black;
      transition: 200ms ease-in-out;
      &:hover {
        color: $orange;
      }
      &.active {
        color: $orange;
      }
    }
    ul {
      display: flex;
      flex-direction: row;
      gap: 30px;
      list-style: none;
      li {
        cursor: pointer;
          .icon{
            position: relative;
          }
          .quantity{
            position: absolute;
            top: 0;
            right: 50px;
            background-color: $orange;
            border-radius: 50px;
            padding: 3px;
            font-size: $font-s;
            font-weight: bolder;
          }
      }
    }
  }
}

.loading{
  position: fixed;
  top: 25%;
  left: 50%;
}

@include media-nav{
  .header{
    padding: 0em 0.1em;
    font-size: 16px;
    nav{
      ul{
        gap: 15px;
        li{
          .quantity{
            right: 5px;
            padding: 2px;
          }
        }
        
      }
    }
  }
}

@include media-s{
  .header{
    padding: 10px 5px;
    img{
      width: 85px;
    }
      nav{
        ul{
          gap: 12px;
          li{
            .quantity{
              top: 1px;
            }
          }
        }
      }
  }
}